@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:ital,wght@0,100;0,200;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'IBM Plex Sans', sans-serif;
}
*:focus {
  outline: none;
}

.App{
display: flex;
flex-direction: column;
width: 100%;
-webkit-backdrop-filter: grayscale(30%);
        backdrop-filter: grayscale(30%);

}
.groupbody{
    margin: 32px 200px 98px 200px;
}
.groupbody__main{
    display: flex;
    justify-content: space-between;
    /* align-items: center; */
    margin-top: 12px;
}
.postcardmain__container{
    max-height: 2000px;
    overflow-y: scroll;
    margin-top: 28px;
}
div::-webkit-scrollbar{
    width: 1%;
   }
   div::-webkit-scrollbar-thumb{
     background-color: grey;
   }
   div::-webkit-scrollbar-track{
     background-color: white;
   }
.createPost{
    background: linear-gradient(180deg, #FF5C5C 0%, #F0568A 100%);
box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.18), 0px 0px 2px rgba(0, 0, 0, 0.08);
    position: static;
    float: right;
    right: 10%;
    color: white !important;
    border-radius: 50%;
    display: none;
}
.createPost >.MuiButtonBase-root>.MuiSvgIcon-root{
    color: white !important;
}
@media screen and (min-width:1220px) {
    .groupbody__main{
        margin: auto 1rem;
    }
}
@media screen and (max-width:735px) {
    .groupbody{
        margin: 0 1rem;
    }
}
@media screen and (max-width:1240px) {
    .groupbody__locContainer{
        display: none;
    }
    .createPost{
        display: block;
    }
}
.CompEng{
    background-image: linear-gradient(180deg, rgba(0, 0, 0, 0.45) 0%, rgba(0, 0, 0, 0.6) 100%), url(/AGT/static/media/Compimg.0b2dfdb7.png);
    height: 440px;
    display: flex;
    align-items: flex-end;
    padding-left: 15rem;
    padding-bottom: 5rem;
    /* background-position: center; */
    /* background-repeat: no-repeat; */
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    /* object-fit: cover; */
    
}
.CompEng__Info{
    color: white;
}
.CompEng__joinNow{
   display: none;

}
@media screen and (max-width:600px) {
    .CompEng__joinNow{
        position: absolute;
        top: 5%;
        right: 3%;
        color: white;
        padding: 6px 8px;
        border: 1px solid white;
        border-radius: 4px;
        display: block;
        cursor: pointer;
    
    }
    .CompEng__joinNow:hover{
        background-color: white;
        color: black;
    }
}
@media screen and (max-width:900px) {
    .CompEng{
        padding: 50px;
    }
}
.postcard{
    display: flex;
    flex-direction: column;
    justify-content: center;
    /* align-items: center; */
    border: 1px solid #E0E0E0;
    border-radius: 4px;
    margin-bottom: 1em;
}
.postcard__title{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 10px;
}
.postcard__Reach{
    display: flex;
    justify-content: space-between;
    margin-top: 32px;
}
.postcard__viewsContainer{
    display: flex;
    align-items: center;
}
.postcard__imgContainer > img{
    object-fit: contain;
    width: 100%;
}
.postcard__infocontainer{
    display: flex;
    flex-direction: column;
    padding: 20px;
}
.postcart__userAvatar{
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 500;
}
.postcard__views{
    display: flex;
    align-items: center;
}
.postcard__shareIcon{
    background: #EDEEF0;
border-radius: 2px;
margin-left: 40px;
}
.postcard__infocontainer > p{
    color:  #5C5C5C;
    margin-top: 12px;
}
.postCard__locationContainer{
    display: flex;
    align-items: center;
    margin-top: 12px;

}
.postcard__Loc{
    display: flex;
    align-items: center;
  
}
.postcard__Loc > p{
    margin-left: 5px;
    font-weight: 500;
}
.postcard__desi{
    margin-right: 44.77px;
    font-weight: 500;
}
.postCard__desi__link{
    background: #FFFFFF;
border: 0.7px dashed #A9AEB8;
box-sizing: border-box;
border-radius: 10px;
text-align :center;
padding: 9px;
margin-top: 1rem;
}
.postcard__views > p{
    margin-left: 8.75px;
}
.post__moreOption{
    width: 160px;
    height: 102px;
    background: #FFFFFF;
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.16), 0px 0px 2px rgba(0, 0, 0, 0.08);
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: flex-start;
    border-radius: 4px;
    z-index: 2;
}
.moreOption{
    margin-left: 12px;
    width: 90%;
    cursor: pointer;
}
.moreOption:hover{
    background-color: lightgray;
}
.active{
    display: none;
}
.postcard__moreOption__buttongrp{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}
.groupNav__navbar{
    display: flex;
    margin-top: 28px;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 12px;
}
.groupNav__navLink{
    margin: 0 20px;
    font-weight: 400;
    cursor: pointer;
}
.groupNav__buttonContainer{
    display: flex;
    justify-content: center;
    align-items: center;
}
.groupNav__btn{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto 8px;
    padding: 8px 12px;
    cursor: pointer;
}
.grpnavWrite{
    background-color: #EDEEF0;
    font-weight: 500;
}
.grpnavWrite:hover{
    background-color: darkgray;
}
.grpnavjoinGroup{
    background-color: #2F6CE5;
    color: white;
    border-radius: 4px;
}
.grpnavjoinGroup:hover{
    background-color: darkblue;
}
.groupNav__navLink:hover{
    color: lightgray;
}
.groupNav__post{
    display: none;
}
.groupNav__filter{
    display: none;
}
@media screen and (max-width:1240px) {
    .groupNav__post{
        display: block;
    }
    .groupNav__filter{
       
        display: flex;
        justify-content: center;
        align-items: center;
        margin: auto 8px;
        padding: 8px 12px;
        cursor: pointer;
    }
    .groupNav__buttonContainer{
        display: none;
    }
    .groupNav__navLink__holder{
        display: none;
    }
}
.header{
    display: flex;
    padding: 20px;
    justify-content: space-between;
    align-items: center;
    position: -webkit-sticky;
    position: sticky;
}

.header a{
   width: 8rem;
   
}
.header> a >img{
    object-fit: contain;
    width: 100%;

}
/* ////////////Search Container/////////////////////////////// */
.header__searchContainer{
    display: flex;
    align-items: center;
    justify-content: center;
    
}
.header__searchContainer > input{
    width: 360px;
    height: 42px;
    background: #F2F2F2;
    border-radius: 21px;
    padding: 12px 50px;
    border: none;
}
.header__searchContainer > input:focus{
    border: none;
}
/* //////////////////UserAccountStatus////////////////// */
.header__userAccountStatus{
    display: flex;
    align-items: center;
}
.header__userAccountStatus__link{
    color: #2f6ce5;
    font-weight: 700;
    cursor: pointer;
}
@media screen and (max-width:1100px) {
    .header__userAccountStatus{
        display: none;
    }
    /* .header__searchContainer{
        margin-right: 10%;
    } */
    /* .header__searchContainer > input{
        width: auto;
        
    } */
    .header{
        margin: 0 10px;
        /* flex-direction: column; */
        
        text-align: center;
    }
    .header__logo{
        width: 100%;
    }
}
@media screen and (max-width:600px) {
    /* .header__searchContainer{
        margin-right: 15%;
        margin-left: -7%;
    } */
    .header{
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .header__searchContainer{
        margin-right: 2rem;
        margin-top: 10px;
    }
    .header__searchContainer > input{
        width: auto;
        height: auto;
    }
}
.loc{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items:flex-start;
    margin-left: 103px;
    padding-top: 45px;
    height: 50vh;
    width: 240px;
}
.loc__Alert{
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 12px;
}
.recommondedgroup{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    
    height: 20vh;
}
.recommondedgroup__title{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 26px;
    
}
.recommondedgroup__Shown{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 22px;
}
.groupshow{
    display: flex;
    align-items: center;
    justify-content: space-between;
    
}
.loc__container{
    display: flex;
    margin-left: -1.5rem;
}
.loc__container > .MuiTextField-root > .MuiInput-root > input{
    padding-left: 2rem;
    padding-right: 2rem;
}
.recommondedgroup__title >p{
    margin-left: 4.75px;
}
.groupshow >h5{
    margin-left: 12px;
}
.recommonded__followBtn{
    background: #EDEEF0;
border-radius: 14px;
padding: 4px 12px;
font-size: 12px;
cursor: pointer;
}
.loc__showmore{
    text-align: end;
}
.followed{
    background-color: black;
    color: white;
}
.loginSignup{
    position: fixed;
    background: #FFFFFF;
box-shadow: 0px 8px 24px rgba(0, 0, 0, 0.25);
border-radius: 8px;
width: 736px;
height: 513px;
z-index: 100000;
/* top: 10%;
left: 30%; */
-webkit-transform: translate(50%,30%);
        transform: translate(50%,30%);
-webkit-transform: translateX(max-device-width);
        transform: translateX(max-device-width);
/* position:relative; */

flex-direction: column;
justify-content: space-between;
}
.loginheadin__main{
    padding: 17px 58px;
    font-weight: 500;
font-size: 14px;
line-height: 16px;
/* identical to box height, or 114% */
background: #EFFFF4;
border-radius: 8px 8px 0px 0px;

text-align: center;

color: #008A45;
}
.login__manibody{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 408px;
}
/* ////////////////////////////////////////////////////////////////////////SignUp////////////////////////////////////// */
.SignUp{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;

}
.SignUp__form{
    display: flex;
    flex-direction: column;
   
}
.SignUp__Name{
    display: flex;

}
.SingUp__Password__Container{
    display: flex;
}
.SignupwithFacebook{
    display: flex;
    justify-content: center;
    align-items: center;
}
.SignupwithGoogle{
    display: flex;
    justify-content: center;
    align-items: center;
}
.SignUp__FirstName,.SignUp__LastName,.SignUp__Email,.Signup__Password,.ConfirmPassword{
    padding: 15px 12px;
    background-color: #F7F8FA;
    border: 1px solid #D9D9DB;
box-sizing: border-box;
border-radius: 2px 2px 0px 0px;
width: 320px;
}
.SignUp__LastName,.SignUp__FirstName{
width: 160px;
}
.CreateAccount{
    text-align: center;
    background: #2F6CE5;
border-radius: 20px;
font-weight: 600;
font-size: 14px;
color: white;
line-height: 16px;
padding: 12px 110px;
margin-top: 19px;
cursor: pointer;
}
.SignupwithFacebook,.SignupwithGoogle{
    /* text-align: center; */
    background: #FFFFFF;
    width: 320px;
border: 0.6px solid #D9D9DB;
box-sizing: border-box;
border-radius: 2px;
padding: 12px;
font-weight: 600;
font-size: 14px;
line-height: 16px;
cursor: pointer;
margin-top: 19px;
}
/* /////////////////////////////////////////SignUpHeading////////////////////////// */
.SignUpHeading{
    display: flex;
    align-items: center;
    justify-content: space-around;
}
.SignUpHeading > p> span{
    color:#2F6CE5;
    cursor: pointer;
}
/* //////////////// */
.forgetPass{
    padding: 12px;
font-weight: 600;
font-size: 14px;
line-height: 16px;
cursor: pointer;
margin-top: 19px;
width: 320px;
text-align: center;
}
@media screen and (max-width:1400px) {
    .loginSignup{
        position: absolute;
        -webkit-transform: translate(30%,50%);
                transform: translate(30%,50%);
    }
}
@media screen and (max-width:1100px) {
    .loginSignup{
        display: none;
    }
}
